<template>
  <div style="width:100%">
     <el-row>
       <el-button type="primary" @click="newAdd"  style="float:right">新建</el-button>
     </el-row>
    <!-- 列表 -->
    <el-table :data="tableData" border v-loading="loading">
      <el-table-column type="index" width="50" label="序号">
      </el-table-column>
			<el-table-column header-align="center" align="center" label="操作"  min-width="150">
				<template slot-scope="scope" v-if ="scope.row.route_name!='root'">
   
					<el-button type="primary" size="mini" @click = "edit_query(scope.row, scope.$index)">修改</el-button>
          <el-button type="danger" size="mini" @click = "del_query(scope.row, scope.$index)">删除</el-button>
         
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="菜单名称"  >
				<template slot-scope="scope">
					<span>{{scope.row.route_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="类型"  >
				<template slot-scope="scope" v-if ="scope.row.route_name!='root'">
					<span>{{scope.row.route_type=='1'?'页面':'非页面'}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="排序"  >
				<template slot-scope="scope">
					<span>{{scope.row.id}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="唯一key"  >
				<template slot-scope="scope">
					<span>{{scope.row.route_key}}</span>
				</template>
			</el-table-column>
			<!-- <el-table-column header-align="center" align="center" label="控制器名"  >
				<template slot-scope="scope">
					<span>{{scope.row.controll_name}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="方法名"  >
				<template slot-scope="scope">
					<span>{{scope.row.method_name}}</span>
				</template>
			</el-table-column> -->
      <el-table-column header-align="center" align="center" label="URL地址"  >
				<template slot-scope="scope">
					<span>{{scope.row.route_url}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="描述"  >
				<template slot-scope="scope">
					<span>{{scope.row.describe}}</span>
				</template>
			</el-table-column>
       <el-table-column header-align="center" align="center" label="状态"  >
				<template slot-scope="scope">
					{{scope.row.status==1?'正常':'停用'}}
				</template>
			</el-table-column>
    
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
    <!-- 新建菜单 -->
      <el-dialog :title="title1" :visible.sync="dialogFormOneVisible">
        <el-form label-width="120px" label-position="left" ref="form1" :model="form_one_data" :rules="rules1">
          <el-form-item label="菜单名称" class="ml15" prop="route_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.route_name"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="类型" class="ml15" prop="route_type">
            <el-col :span="12">
              <el-radio v-model="form_one_data.route_type" label="1">页面</el-radio>
              <el-radio v-model="form_one_data.route_type" label="2">非页面</el-radio>
            </el-col>
          </el-form-item>
           <el-form-item label="URL地址" class="ml15" prop="route_url">
            <el-col :span="12">
              <el-input v-model="form_one_data.route_url" :disabled="form_one_data.route_type==2"></el-input>
            </el-col>
          </el-form-item>
           <el-form-item label="唯一key" class="ml15" prop="route_key">
            <el-col :span="12">
              <el-input v-model="form_one_data.route_key"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="描述" class="ml15" prop="describe">
            <el-col :span="12">
              <el-input v-model="form_one_data.describe" type="textarea"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="后台控制器名" class="ml15" prop="controll_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.controll_name"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="后台方法名" class="ml15" prop="method_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.method_name"></el-input>
            </el-col>
        </el-form-item>
          <el-form-item label="上级菜单" class="ml15" prop="parent_id" >
            <el-col :span="12">
               <el-select v-model="form_one_data.parent_id" placeholder="请选择">
                <el-option
                  v-for="item in tableData"
                  :key="item.value"
                  :label="item.route_name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-col>
          </el-form-item>
          <el-form-item label="状态" class="ml15" prop="status">
            <el-col :span="12">
              <el-radio v-model="form_one_data.status" label="1">启用</el-radio>
              <el-radio v-model="form_one_data.status" label="2">停用</el-radio>
            </el-col>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button type="primary"  @click="add_query">确 定</el-button>
        </div>
      </el-dialog>
  </div>
</template>
<script>
  import {routeList,addRoutes,routeDel,routeDetail} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data(){
      return{
            title1:'新建菜单',
            queryStr: {
                total: 1,       // 总数
                page: 1,        // 当前分页
                size: 10,      // 每页条数
            },
            form_one_data:{
                route_name:'',
                route_type:'',
                route_key:'',
                parent_id:0,
                route_url:'',
                controll_name:'',
                status:"1",
                method_name:'',
                describe:'',
            },
            rules1: {
                route_name: [
                    { required: true, message: '请输入菜单名称', trigger: 'blur' },
                ],
                route_type: [
                    { required: true, message: '请选择类型', trigger: 'blur' },
                ],
                route_key: [
                    { required: true, message: '请输入唯一key', trigger: 'blur' },
                ],
                route_url: [
                    { required: true, message: '请输入url', trigger: 'blur' },
                ],
                parent_id: [
                    { required: true, message: '请选择上级菜单', trigger: 'blur' },
                ],
                status: [
                    { required: true, message: '请选择状态', trigger: 'blur' },
                ],
            },
            dialogFormOneVisible: false,
            tableData:[],
            loading: false,//loading
        }
    },
    mounted(){
        this.query()
    },
    methods:{
      // 查询
        query(run_type) { 

            let params = {
                page: this.queryStr.page,
                limit: this.queryStr.limit,
            };
            routeList(params).then((res) => {
                this.queryStr.total = Number(res.data.data.total)?Number(res.data.data.total):1;
                this.tableData = res.data.data;
                console.log(this.tableData);
            });
        },
        newAdd(){
            this.title1="新建";
            this.form_one_data={};
            this.dialogFormOneVisible=true;
        },
        add_query(){
            this.$refs['form1'].validate((valid) => {
                if (valid) {
                    let params = this.form_one_data;
                    addRoutes(params).then((res) => {
                        this.$message.success(res.data.msg);
                        this.dialogFormOneVisible =false;
                        this.title1='新建菜单';
                        this.query();
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        del_query(val,index){ //删除
            let id = val.id;
            let params = {
            'id':id,
            }
            routeDel(params).then((res) => {
                this.$message.success('【删除】操作成功!');
                this.query();
            });
        },
        // 修改 yjq
        edit_query(val, index){
            this.title1 ='编辑菜单';
            let id = val.id;
            let params = {
                'id':id,
            }
            routeDetail(params).then((res) => {
                this.form_one_data = res.data.data;
                this.dialogFormOneVisible = true;
            });
        },
        // 分页器
        currentChange(val) {
            
            this.queryStr.page = val;
            this.query();
        },
        // 时间戳的转换
        changeTime(time){
            let year = new Date((time)).getFullYear();
            let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
            let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
            let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
            let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
            let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
            return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
        }
    }
  }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





